
<template>
  <div id = "#app" >
<el-col :span="12">
  <el-form style="margin-top:20px;" :inline="true" >
      <el-form-item >
        <el-date-picker
        v-model="startDate"
        align="right"
        type="date"
        placeholder="选择开始日期"
        format="yyyy 年 MM 月 dd 日"
        value-format="yyyy-MM-dd">
        </el-date-picker>

      </el-form-item>

      <el-form-item>
        <el-date-picker
        v-model="endDate"
        align="right"
        type="date"
        placeholder="选择结束日期"
        format="yyyy 年 MM 月 dd 日"
        value-format="yyyy-MM-dd">
      </el-date-picker>

      </el-form-item>
      <el-form-item>
          <el-select v-model="origin">
              <el-option v-for="item in origins" v-bind:data="item" v-bind:key="item.value" :label="item.label" :value="item.value">
              </el-option>
          </el-select>
      </el-form-item>
      <el-form-item>
          <el-select v-model="statisticType">
              <el-option v-for="item in statisticTypes" v-bind:data="item" v-bind:key="item.value" :label="item.label" :value="item.value">
              </el-option>
          </el-select>
      </el-form-item>

      <el-form-item>
          <el-button type="primary" @click="dateQuery">查询</el-button>
      </el-form-item>
  </el-form>
          <ve-line :data="dateChartData" :title="dateStatisticTitle" :settings="dateLineChartSettings" :toolbox="toolbox"></ve-line>
</el-col>
<el-col :span="12">
  <el-form style="margin-top:20px;" :inline="true" >
      <el-form-item >
        <el-date-picker
        v-model="startMonth"
        align="right"
        type="month"
        placeholder="选择开始月份"
        format="yyyy 年 MM 月"
        value-format="yyyy-MM">
        </el-date-picker>

      </el-form-item>

      <el-form-item>
        <el-date-picker
        v-model="endMonth"
        align="right"
        type="month"
        placeholder="选择结束月份"
        format="yyyy 年 MM 月"
        value-format="yyyy-MM">
      </el-date-picker>

      </el-form-item>
      <el-form-item>
          <el-select v-model="origin">
              <el-option v-for="item in origins" v-bind:data="item" v-bind:key="item.value" :label="item.label" :value="item.value">
              </el-option>
          </el-select>
      </el-form-item>
      <el-form-item>
          <el-select v-model="statisticType">
              <el-option v-for="item in statisticTypes" v-bind:data="item" v-bind:key="item.value" :label="item.label" :value="item.value">
              </el-option>
          </el-select>
      </el-form-item>

      <el-form-item>
          <el-button type="primary" @click="monthQuery">查询</el-button>
      </el-form-item>
  </el-form>
          <ve-line :data="monthChartData" :title="monthStatisticTitle" :settings="monthLineChartSettings" :toolbox="toolbox"></ve-line>
</el-col>
<el-col :span="12">
  <el-form style="margin-top:20px;" :inline="true" >
      <el-form-item >
        <el-date-picker
        v-model="sYear"
        align="right"
        type="year"
        placeholder="选择开始年份"
        format="yyyy 年"
        value-format="yyyy">
        </el-date-picker>

      </el-form-item>

      <el-form-item>
        <el-date-picker
        v-model="endYear"
        align="right"
        type="year"
        placeholder="选择结束年份"
        format="yyyy 年"
        value-format="yyyy">
      </el-date-picker>

      </el-form-item>
      <el-form-item>
          <el-select v-model="origin">
              <el-option v-for="item in origins" v-bind:data="item" v-bind:key="item.value" :label="item.label" :value="item.value">
              </el-option>
          </el-select>
      </el-form-item>
      <el-form-item>
          <el-select v-model="statisticType">
              <el-option v-for="item in statisticTypes" v-bind:data="item" v-bind:key="item.value" :label="item.label" :value="item.value">
              </el-option>
          </el-select>
      </el-form-item>

      <el-form-item>
          <el-button type="primary" @click="yearQuery">查询</el-button>
      </el-form-item>
  </el-form>
          <ve-line :data="yearChartData" :title="yearStatisticTitle" :settings="yearLineChartSettings" :toolbox="toolbox"></ve-line>
</el-col>



</div>
</template>



<script>
import {
    dateQuery,
    monthQuery,
    yearQuery
} from 'components/api/report/rechargestatistic.js'

export default {
    data() {
        return {
          startDate:"",
          endDate:"",
          startMonth:"",
          endMonth:"",
          sYear:"",
          endYear:"",
          dateStatisticTitle:{
            text: '用户充值按天统计'
          },
          monthStatisticTitle:{
            text: '用户充值按月统计'
          },
          yearStatisticTitle:{
            text: '用户充值按年统计'
          },
          dateChartData:{
            columns:["period","quantity"],
            rows:[],

          },
          dateLineChartSettings:{
            dimension:["period"],
            metrics:["quantity"],
            yAxisName:["增长人数"],
            area: false
          },
          monthChartData:{
            columns:["period","quantity"],
            rows:[],

          },
          monthLineChartSettings:{
            dimension:["period"],
            metrics:["quantity"],
            yAxisName:["增长人数"],
            area: false
          },
          yearChartData:{
            columns:["period","quantity"],
            rows:[],

          },
          yearLineChartSettings:{
            dimension:["period"],
            metrics:["quantity"],
            yAxisName:["增长人数"],
            area: false
          },
        toolbox :{
          feature: {
            dataView: {readOnly: false},
            magicType: {type: ['line', 'bar']},
            saveAsImage: {}
          }
        },
            origin: "",
            statisticType: "",
            origins:[
                {label:"--来源--",value:""},
                {label:"PC",value:"PC"},
                {label:"Gateway",value:"Gateway"},
                {label:"WeChat",value:"WeChat"},
                {label:"IOS",value:"IOS"},
                {label:"Android",value:"Android"}
            ],
            statisticTypes:[
                {label:"--提现状态--",value:""},
                {label:"提现申请",value:0},
                {label:"提现成功",value:1}
            ]
    }
    },
    methods: {

        dateQuery() {
            let para = {
              startDate:this.startDate.format("yyyyMMdd"),
              endDate:this.endDate.format("yyyyMMdd"),
                statisticType:this.statisticType,
                origin:this.origin
            }
            console.log(para);
            dateQuery(para).then(resp =>{
              this.dateChartData.rows = resp.data;
              console.log(this.dateChartData);
            });

            },
            monthQuery() {
                let para = {
                  startMonth:this.startMonth.format("yyyyMM"),
                  endMonth:this.endMonth.format("yyyyMM"),
                    statisticType:this.statisticType,
                    origin:this.origin
                }
                console.log(para);
                monthQuery(para).then(resp =>{
                  this.monthChartData.rows = resp.data;
                });

              },
              yearQuery() {
                  console.log(this.sYear);
                  console.log(this.sYear.format("yyyy"));

                  let para = {
                    startYear:this.sYear.format("yyyy"),
                    endYear:this.endYear.format("yyyy"),
                      statisticType:this.statisticType,
                      origin:this.origin
                  }
                  console.log(para);
                  yearQuery(para).then(resp =>{
                    this.yearChartData.rows = resp.data;
                  });

                }





    },
    mounted() {
        this.endDate = new Date();
        this.startDate = new Date(new Date().getTime()-7*24*3600*1000);
        this.endMonth = new Date();
        this.startMonth = new Date(new Date().getTime()-90*24*3600*1000);
        this.endYear = new Date();
        this.sYear = new Date();
        this.dateQuery();
        this.monthQuery();
        this.yearQuery();

    }
};
</script>
<style>
.buttons {
    margin-top: 20px;
}
</style>



