import axios from 'axios';
let base = '/report/rechargeStatistic/';

axios.defaults.headers.common['accessToken'] = sessionStorage.getItem('user');


 export const dateQuery = params => {
    return axios.post(`${base}/statisticbydate`, params );

  };

  export const monthQuery = params => {
     return axios.post(`${base}/statisticbymonth`,  params );

   };

   export const yearQuery = params => {
      return axios.post(`${base}/statisticbyyear`,  params );

    };



